.navbar {
  box-sizing: border-box;
  width: 100%;
  height: 100px;
  display: flex;
  justify-content: space-between;
  place-items: center;
  padding: 0px 40px;
  position: absolute;
}
.navbar .title {
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 23px;
  color: white;
  font-family: "Poppins", sans-serif;
  text-shadow: 0px 0px 20px #ffffff00;
  text-decoration: underline;
  text-underline-offset: 7px;
  transition: all 0.2s ease-in;
}
.navbar .title:hover,
.navlinks a:hover {
  color: #ffffff;
  text-decoration: underline;
  text-underline-offset: 2px;
  text-shadow: 0px 0px 20px #ffffff;
}
.navlinks {
  display: flex;
  justify-content: center;
  place-items: center;
  gap: 25px;
  font-size: 23px;
  padding: 10px 20px;
  background: #9292ff1d;
  backdrop-filter: blur(10px);
  border-radius: 10px;
  z-index: 100;
}
.navlinks a {
  display: flex;
  place-items: center;
  gap: 5px;
  color: #ffffff;
  text-shadow: 0px 0px 30px #003cff;
  transition: all 0.2s ease-in;
  text-decoration: none;
  text-underline-offset: 7px;
}
.menuBar {
  width: 100%;
  max-width: 500px;
  height: 70px;
  background: #9292ff1d;
  backdrop-filter: blur(10px);
  box-shadow: 0px 0px 0px #ffffff00;
  position: fixed;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  z-index: 100;
  border-radius: 20px 20px 0px 0px;
  transition: all 0.5s ease-in-out;
  display: flex;
  justify-content: center;
  place-items: center;
}
.menuBar:hover {
  box-shadow: 0px 0px 60px 30px #afafff;
}
.menuButtons {
  display: flex;
  gap: 30px;
  font-family: "Poppins", sans-serif;
  font-size: 20px;
  color: white;
}
.menuButtons div {
  display: flex;
  justify-content: center;
  place-items: center;
  color: #003cff;
  background: white;
  border: none;
  border-radius: 50px;
  width: 50px;
  height: 50px;
  position: relative;
  transition: all 0.2s ease-in-out;
  box-shadow: 0px 110px 0px #ffffff;
}
.menuButtons div:hover {
  transform: translateY(8px);
  box-shadow: 0px 20px 0px #ffffff;
}
.menuButtons div::before {
  transform: translateY(-8px) translateX(1px);
  position: absolute;
  background: #ff000000;
  color: transparent;
  width: 50px;
  height: 50px;
  left: -51px;
  top: 10px;
  border-radius: 50px;
  z-index: -1;
  box-shadow: 20px 20px 0px #ffffff;
}
.menuButtons div::after {
  transform: translateY(-8px) translateX(-2px);
  position: absolute;
  background: #33ff0000;
  color: transparent;
  width: 50px;
  height: 50px;
  left: 51px;
  top: 10px;
  border-radius: 50px;
  box-shadow: -20px 20px 0px #ffffff;
  z-index: -1;
}
.menuButtons .item.active {
  transform: translateY(8px);
  box-shadow: 0px 20px 0px #ffffff;
}
.menuButtons .item:hover::after {
  content: " ";
  animation: rise-after 0.2s alternate ease-in;
}
.menuButtons .item:hover::before {
  animation: rise-before 0.2s alternate ease-in;
  content: " ";
}
@keyframes rise-after {
  0% {
    transform: translateY(40px) translateX(-1px);
  }
  100% {
    transform: translateY(-8px) translateX(-1px);
  }
}

@keyframes rise-before {
  0% {
    transform: translateY(40px) translateX(1px);
  }
  100% {
    transform: translateY(-8px) translateX(1px);
  }
}
